import styled from "styled-components";
const ContentContainer = styled.div`
  height: auto;
  width: 100%;
  color: #fff;
  padding: 20px;
  max-width: 650px;
  margin: 0 auto;
  @media only screen and (max-width: 600px) {
    width: 100%;
    padding: 10px;
  }
`;
export default function Container({ children }) {
  return <ContentContainer>{children}</ContentContainer>;
}
