import styled from "styled-components";
import HeaderBar from "../components/Header";
import Fab from "../components/Fab";
import { NavigationStack } from "../components/Tabs";
import { useContext, useState } from "react";
import { ThemeContext } from "../context";
import HashLoader from "react-spinners/HashLoader";
import AnswerTab from "./AnswerTab";
import NotAnsweredTab from "./NotAnsweredTab";
import { Dialog } from "./Dialog";
import { Plus } from "react-feather";

const Container = styled.div`
  min-height: 100vh;
  width: 100vw;
  background: ${(props) => props.bg};
`;

const LoadingContainer = styled.div`
  min-height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const DialogInput = styled.input`
  display: block;
  width: 100%;
  height: 40px;
  border: none;
  padding: 10px;
  border-radius: 4px;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  ::placeholder {
    color: rgba(255, 255, 255, 0.5);
  }
`;

export default function HomeScreen() {
  const { background } = useContext(ThemeContext);
  const [showDialog, setShowDialog] = useState(true);
  const handleDialogClose = () => setShowDialog(false);
  const handleDialogOpen = () => setShowDialog(true);
  const [question, setQuestion] = useState("");
  const handleQuestionChange = (e) => setQuestion(e.target.value);
  return (
    <Container bg={background}>
      <HeaderBar />
      <NavigationStack tabs={[{ name: "Answered" }, { name: "Not Answered" }]}>
        {(activeTab, isLoading) => (
          <>
            {isLoading ? (
              <LoadingContainer>
                <HashLoader color="#fff" size={30} />
              </LoadingContainer>
            ) : (
              <>{activeTab === 0 ? <AnswerTab /> : <NotAnsweredTab />}</>
            )}
          </>
        )}
      </NavigationStack>
      <Dialog
        title={question || "Ask a cool question?"}
        secondaryTitle="Please make sure you do not ask any personal question and that you are honest enough to ask the questioni? You understand or what?"
        show={showDialog}
        onClose={handleDialogClose}
      >
        <DialogInput
          onChange={handleQuestionChange}
          type="text"
          placeholder="Please type your question"
        />
      </Dialog>
      <Fab onClick={handleDialogOpen}>
        <Plus />
      </Fab>
    </Container>
  );
}
